a.meanmenu-reveal {
  display: none
}

.mean-container .mean-bar {
  background: inherit;
  float: left;
  min-height: 0;
  position: relative;
  width: 100%;
  z-index: 1;
}

.mean-container a.meanmenu-reveal {
  color: #333;
  cursor: pointer;
  display: block;
  float: right;
  font-size: 1px;
  font-weight: 700;
  height: 18px;
  line-height: 22px;
  padding: 2px 5px;
  position: absolute;
  right: 0;
  text-decoration: none;
  top: -65px;
  width: 18px;
  border: 1px solid #333;
}

.mean-container a.meanmenu-reveal span {
  display: block;
  background: #333;
  height: 2px;
  margin-top: 3px
}

.mean-container .mean-nav {
  float: left;
  width: 100%;
  background: transparent
